import { useCallback } from 'react';
import { useToastStore } from '../store/toast';
import { classifyError, getDisplayMessage, logError } from '../utils/errorHandler';
import type { AppError } from '../utils/errorHandler';

export interface ErrorHandlerOptions {
  showToast?: boolean;
  logToConsole?: boolean;
  context?: string;
  fallbackMessage?: string;
}

export function useErrorHandler() {
  const { showError, showWarning } = useToastStore();

  const handleError = useCallback(
    (
      error: unknown,
      options: ErrorHandlerOptions = {}
    ): AppError => {
      const {
        showToast = true,
        logToConsole = true,
        context,
        fallbackMessage
      } = options;

      // ã‚¨ãƒ©ãƒ¼ã‚’åˆ†é¡
      const appError = classifyError(error);

      // ã‚³ãƒ³ã‚½ãƒ¼ãƒ«ãƒ­ã‚°å‡ºåŠ›
      if (logToConsole) {
        logError(appError, context);
      }

      // ãƒ¦ãƒ¼ã‚¶ãƒ¼ã«è¡¨ç¤ºã™ã‚‹ãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã‚’æ±ºå®š
      const displayMessage = fallbackMessage || getDisplayMessage(appError);

      // ãƒˆãƒ¼ã‚¹ãƒˆè¡¨ç¤º
      if (showToast) {
        if (appError.type === 'validation') {
          showWarning(displayMessage);
        } else {
          showError(displayMessage);
        }
      }

      return appError;
    },
    [showError, showWarning]
  );

  // éåŒæœŸé–¢æ•°ã®ã‚¨ãƒ©ãƒ¼ãƒãƒ³ãƒ‰ãƒªãƒ³ã‚°ç”¨ãƒ˜ãƒ«ãƒ‘ãƒ¼
  const handleAsyncError = useCallback(
    async <T>(
      asyncFn: () => Promise<T>,
      options: ErrorHandlerOptions = {}
    ): Promise<T | null> => {
      try {
        return await asyncFn();
      } catch (error) {
        handleError(error, options);
        return null;
      }
    },
    [handleError]
  );

  // è¤‡æ•°ã®ã‚¨ãƒ©ãƒ¼ã‚’å‡¦ç†ã™ã‚‹ãƒ˜ãƒ«ãƒ‘ãƒ¼
  const handleBatchErrors = useCallback(
    (
      errors: Array<{ error: unknown; context?: string }>,
      options: ErrorHandlerOptions = {}
    ): AppError[] => {
      return errors.map(({ error, context }) =>
        handleError(error, { ...options, context })
      );
    },
    [handleError]
  );

  return {
    handleError,
    handleAsyncError,
    handleBatchErrors
  };
}
