// ã‚¨ãƒ©ãƒ¼ã®ç¨®é¡ã‚’å®šç¾©
export enum ErrorType {
  VALIDATION = 'validation',
  NETWORK = 'network',
  DATABASE = 'database',
  PARSE = 'parse',
  PERMISSION = 'permission',
  UNKNOWN = 'unknown',
}

// ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹å›ºæœ‰ã®ã‚¨ãƒ©ãƒ¼ã‚¿ã‚¤ãƒ—ã‚’è¿½åŠ 
export enum DbErrorType {
  CONNECTION_FAILED = 'connection_failed',
  TRANSACTION_FAILED = 'transaction_failed',
  DATA_CORRUPTED = 'data_corrupted',
  QUOTA_EXCEEDED = 'quota_exceeded',
  VERSION_ERROR = 'version_error',
  UNKNOWN = 'unknown',
}

// ã‚¨ãƒ©ãƒ¼æƒ…å ±ã®å‹å®šç¾©
export interface AppError {
  type: ErrorType;
  message: string;
  originalError?: Error;
  context?: Record<string, unknown>;
}

// ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹ã‚¨ãƒ©ãƒ¼æƒ…å ±ã®å‹å®šç¾©
export interface DbError {
  type: DbErrorType;
  message: string;
  originalError?: unknown;
  retryable: boolean;
}

// ã‚¨ãƒ©ãƒ¼åˆ†é¡æ©Ÿèƒ½
export function classifyError(error: unknown): AppError {
  // DbErrorã‚’AppErrorã«å¤‰æ›
  if (
    error &&
    typeof error === 'object' &&
    'type' in error &&
    'retryable' in error &&
    'message' in error
  ) {
    const errorObj = error as Record<string, unknown>;
    if (Object.values(DbErrorType).includes(errorObj.type as DbErrorType)) {
      const dbError = error as DbError;
      return {
        type: ErrorType.DATABASE,
        message: dbError.message,
        originalError: dbError.originalError as Error,
        context: {
          dbErrorType: dbError.type,
          retryable: dbError.retryable,
        },
      };
    }
  }

  if (error instanceof Error) {
    const message = error.message.toLowerCase();

    // ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹é–¢é€£ã‚¨ãƒ©ãƒ¼
    if (
      message.includes('indexeddb') ||
      message.includes('database') ||
      message.includes('store')
    ) {
      return {
        type: ErrorType.DATABASE,
        message: 'ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹æ“ä½œã§ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
        originalError: error,
      };
    }

    // ãƒ‘ãƒ¼ã‚¹é–¢é€£ã‚¨ãƒ©ãƒ¼
    if (
      message.includes('parse') ||
      message.includes('json') ||
      message.includes('csv')
    ) {
      return {
        type: ErrorType.PARSE,
        message: 'ãƒ‡ãƒ¼ã‚¿ã®è§£æã§ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
        originalError: error,
      };
    }

    // ãƒãƒªãƒ‡ãƒ¼ã‚·ãƒ§ãƒ³é–¢é€£ã‚¨ãƒ©ãƒ¼
    if (
      message.includes('validation') ||
      message.includes('å¿…é ˆ') ||
      message.includes('å½¢å¼')
    ) {
      return {
        type: ErrorType.VALIDATION,
        message: error.message,
        originalError: error,
      };
    }

    // ãƒãƒƒãƒˆãƒ¯ãƒ¼ã‚¯é–¢é€£ã‚¨ãƒ©ãƒ¼
    if (
      message.includes('network') ||
      message.includes('fetch') ||
      message.includes('request')
    ) {
      return {
        type: ErrorType.NETWORK,
        message: 'ãƒãƒƒãƒˆãƒ¯ãƒ¼ã‚¯ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
        originalError: error,
      };
    }

    return {
      type: ErrorType.UNKNOWN,
      message: error.message,
      originalError: error,
    };
  }

  return {
    type: ErrorType.UNKNOWN,
    message: 'äºˆæœŸã—ãªã„ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸ',
    context: { originalError: error },
  };
}

// ãƒ¦ãƒ¼ã‚¶ãƒ¼ãƒ•ãƒ¬ãƒ³ãƒ‰ãƒªãƒ¼ãªã‚¨ãƒ©ãƒ¼ãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã‚’ç”Ÿæˆ
export function getDisplayMessage(appError: AppError): string {
  switch (appError.type) {
    case ErrorType.VALIDATION:
      return appError.message;

    case ErrorType.DATABASE:
      // ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹å›ºæœ‰ã®ã‚¨ãƒ©ãƒ¼ãƒ¡ãƒƒã‚»ãƒ¼ã‚¸ã‚’ã‚«ã‚¹ã‚¿ãƒã‚¤ã‚º
      if (appError.context?.dbErrorType) {
        const dbErrorType = appError.context.dbErrorType as DbErrorType;
        const isRetryable = appError.context.retryable as boolean;

        switch (dbErrorType) {
          case DbErrorType.QUOTA_EXCEEDED:
            return 'ã‚¹ãƒˆãƒ¬ãƒ¼ã‚¸å®¹é‡ãŒä¸è¶³ã—ã¦ã„ã¾ã™ã€‚ä¸è¦ãªãƒ‡ãƒ¼ã‚¿ã‚’å‰Šé™¤ã—ã¦ã‹ã‚‰å†åº¦ãŠè©¦ã—ãã ã•ã„ã¾ã›ã€‚';
          case DbErrorType.CONNECTION_FAILED:
            return isRetryable
              ? 'ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹æ¥ç¶šã«å¤±æ•—ã—ã¾ã—ãŸã€‚ã—ã°ã‚‰ãå¾…ã£ã¦ã‹ã‚‰å†åº¦ãŠè©¦ã—ãã ã•ã„ã¾ã›ã€‚'
              : 'ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹æ¥ç¶šã«å•é¡ŒãŒã‚ã‚Šã¾ã™ã€‚ãƒšãƒ¼ã‚¸ã‚’å†èª­ã¿è¾¼ã¿ã—ã¦ãŠè©¦ã—ãã ã•ã„ã¾ã›ã€‚';
          case DbErrorType.TRANSACTION_FAILED:
            return 'ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹å‡¦ç†ä¸­ã«ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã—ã¾ã—ãŸã€‚ã‚‚ã†ä¸€åº¦ãŠè©¦ã—ãã ã•ã„ã¾ã›ã€‚';
          case DbErrorType.DATA_CORRUPTED:
            return 'ãƒ‡ãƒ¼ã‚¿ãŒç ´æã—ã¦ã„ã‚‹å¯èƒ½æ€§ãŒã‚ã‚Šã¾ã™ã€‚ç®¡ç†ç”»é¢ã‹ã‚‰ãƒ‡ãƒ¼ã‚¿ã®ç¢ºèªã‚’ãŠå‹§ã‚ã„ãŸã—ã¾ã™ã€‚';
          case DbErrorType.VERSION_ERROR:
            return 'ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹ã®æ›´æ–°ä¸­ã§ã™ã€‚ãƒšãƒ¼ã‚¸ã‚’å†èª­ã¿è¾¼ã¿ã—ã¦ãŠè©¦ã—ãã ã•ã„ã¾ã›ã€‚';
          default:
            return (
              appError.message || 'ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹æ“ä½œã§ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã„ãŸã—ã¾ã—ãŸã€‚'
            );
        }
      }
      return 'ãƒ‡ãƒ¼ã‚¿ã®ä¿å­˜ãƒ»èª­ã¿è¾¼ã¿ã«å¤±æ•—ã„ãŸã—ã¾ã—ãŸã€‚ã‚‚ã†ä¸€åº¦ãŠè©¦ã—ãã ã•ã„ã¾ã›ã€‚';

    case ErrorType.PARSE:
      return 'ãƒ•ã‚¡ã‚¤ãƒ«ã®å½¢å¼ãŒæ­£ã—ãã‚ã‚Šã¾ã›ã‚“ã€‚ãƒ•ã‚¡ã‚¤ãƒ«ã®å†…å®¹ã‚’ã”ç¢ºèªãã ã•ã„ã¾ã›ã€‚';

    case ErrorType.NETWORK:
      return 'ãƒãƒƒãƒˆãƒ¯ãƒ¼ã‚¯æ¥ç¶šã«å•é¡ŒãŒã‚ã‚Šã¾ã™ã€‚æ¥ç¶šçŠ¶æ³ã‚’ã”ç¢ºèªãã ã•ã„ã¾ã›ã€‚';

    case ErrorType.PERMISSION:
      return 'ã“ã®æ“ä½œã‚’å®Ÿè¡Œã™ã‚‹æ¨©é™ãŒã‚ã‚Šã¾ã›ã‚“ã€‚';

    default:
      return appError.message || 'äºˆæœŸã—ãªã„ã‚¨ãƒ©ãƒ¼ãŒç™ºç”Ÿã„ãŸã—ã¾ã—ãŸã€‚';
  }
}

// ãƒªãƒˆãƒ©ã‚¤å¯èƒ½ã‹ã©ã†ã‹ã‚’åˆ¤å®š
export function isRetryableError(appError: AppError): boolean {
  if (
    appError.type === ErrorType.DATABASE &&
    appError.context?.retryable !== undefined
  ) {
    return appError.context.retryable as boolean;
  }

  // ãã®ä»–ã®ã‚¨ãƒ©ãƒ¼ã‚¿ã‚¤ãƒ—ã®å ´åˆã®ãƒ‡ãƒ•ã‚©ãƒ«ãƒˆå€¤
  switch (appError.type) {
    case ErrorType.NETWORK:
      return true;
    case ErrorType.DATABASE:
      return true;
    case ErrorType.VALIDATION:
    case ErrorType.PARSE:
    case ErrorType.PERMISSION:
      return false;
    default:
      return false;
  }
}

// ã‚¨ãƒ©ãƒ¼ãƒ­ã‚°å‡ºåŠ›ï¼ˆãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹ã‚¨ãƒ©ãƒ¼ã®è©³ç´°æƒ…å ±ã‚’å«ã‚€ï¼‰
export function logError(appError: AppError, context?: string): void {
  const logMessage = context
    ? `[${context}] ${appError.message}`
    : appError.message;

  if (appError.originalError) {
    console.error(logMessage, appError.originalError);
  } else {
    console.error(logMessage, appError.context);
  }

  // ãƒ‡ãƒ¼ã‚¿ãƒ™ãƒ¼ã‚¹ã‚¨ãƒ©ãƒ¼ã®è©³ç´°æƒ…å ±ã‚’ãƒ­ã‚°å‡ºåŠ›
  if (appError.type === ErrorType.DATABASE && appError.context?.dbErrorType) {
    console.error('Database Error Details:', {
      dbErrorType: appError.context.dbErrorType,
      retryable: appError.context.retryable,
      context: context,
    });
  }
}
